.courses-container {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.course-card {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
  background: white;
}

.course-card:hover {
  transform: translateY(-5px);
}

.course-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.course-info {
  padding: 1rem;
}

.course-info h3 {
  margin: 0 0 0.5rem 0;
  color: #333;
}

.course-info p {
  color: #666;
  margin-bottom: 1rem;
  font-size: 0.9rem;
}

.course-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.course-meta span {
  color: #61dafb;
  font-weight: bold;
}

.view-course-btn {
  display: block;
  text-align: center;
  padding: 0.75rem;
  background-color: #61dafb;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.view-course-btn:hover {
  background-color: #4fa8c7;
}

.create-course-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.create-course-container textarea {
  width: 100%;
  min-height: 150px;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  resize: vertical;
}

.create-course-container textarea:focus {
  outline: none;
  border-color: #61dafb;
} 